<template>
  <div id="faqs">
    <nav-menu></nav-menu>
    <div id="faq-body">
      <h1>FAQs &amp; How To</h1>
      <div id="howtos">
        <div class="howto">
          <h3>I've never heard of Bitcoin/Ethereum</h3>
          <p>Bitcoin &amp; Ethereum are a purely digital currencies that can not be taken down by anyone in the world. They can both be purchased on sites like <a href="http://coinbase.com">Coinbase</a></p>
        </div>
        <div class="howto">
          <h3>I've heard of Bitcoin before but not Etheruem</h3>
          <p>Bitcoin is just an asset of value. Ethereum has the capacity to run programs (called smart contracts) that can not be taken down by anyone in the world.</p>
        </div>
        <div class="howto">
          <h3>I've heard of Ethereum but I haven't used Smart contracts with Metamask/Trustwallet</h3>
          <p>In order to interact with smart contracts on this website, you'll need to install <a href="https://metamask.io/" target="_blank">Metamask</a> on your desktop or Trustwallet on your phone. When you arrive to the exchange page, the website will request access to Metamask, and once you approve you'll be able to interact with the smart contract</p>
        </div>
        <div class="howto">
          <h3>I've used smart contracts before but I want to understand this smart contract more</h3>
          <p>The original code for this project was a flat out pyramid scheme. That pyramid still exists in the code, but a secondary asset is generated in inverse of that pyramid. The distribution of that secondary asset is crucial for governance of Oracles. The pyramid functions as a defense from whales corrupting the supply of resolve tokens. Here is the <a href="https://docs.google.com/document/d/1U2WAPDdsgVq4SsFaWhHTjkkn_P-R6ZusDIKyhS5NWhY" target="_blank">whitepaper</a> for greater depth</p>
        </div>
      </div>
      <h2>FAQs</h2>
      <ul>
        <h3>Rugs</h3>
      </ul>
    </div>
  </div>
</template>

<script>
  import NavMenu from '@/components/Nav-Menu'
  import Countdown from 'vuejs-countdown'

  import Contract from '@/components/Contract'

  export default {
    name: 'Home',
    components:{
      NavMenu,
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #faq-body{padding-top: 30px;
    max-width: 80%;
    margin:0 auto;}
    #howtos{display:grid;
  grid-gap: 10px;
  grid-template-columns: auto auto;}
  .howto{background: #dddddd;
    min-height: 120px;
    padding: 20px;}
    .howto p{text-indent: 30px;}
</style>



