<template>
  <button class="contract-button contract-box" @click="dispatch">
    <slot></slot>
  </button>
</template>

<script>

export default {
  name: 'contract-button',
  props:{
    action: String,
    data: Object
  },
  methods:{
    dispatch(){
      if(this.action){
        if(this.data){//console.log("HITTTTTT")
          this.$store.dispatch(this.action, this.data)
        }
        else{//console.log("TESTSTETS")
          this.$store.dispatch(this.action)
        }
      }
    }
  }
}
</script>

<style scoped>

button{
  background:#f5f5f5;
  border-style:solid;
  border-width:1.5px;
  border-color:#444;
  padding:7px;
  color: #444;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  width:125px;
}

#exchange button{
  border-bottom: 1px solid gold;
  color:#f8f8f8;
  border-radius: 9px;

  color: rgba(0, 0, 0, 0.72);
  text-decoration: none;
  font-weight: bold;
  text-shadow: 1px 1px 0 #ffffff;
  box-shadow:
    2px 2px 0.5em rgba(122, 98, 0, 0.55),
    inset 1px 1px 0 rgba(255, 255, 255, 0.9),
    inset -1px -1px 0 rgba(0, 0, 0, 0.34)
  ;
  
}
</style>



