import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Contract from '@/components/Contract'
import FAQs from '@/components/FAQs'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Contract
    },
    {
      path: '/:masternode',
      name: 'Home',
      component: Contract
    },
    {
      path: '/contract/:masternode',
      name: 'Contract',
      component: Contract
    },
    {
      path: '/WarOnRugs/:masternode',
      name: 'Contract',
      component: Contract
    },
    {
      path: '/contract',
      name: 'Contract',
      component: Contract
    },
    {
      path: '/FAQs',
      name: 'FAQs',
      component: FAQs
    }/*,
    {
      path: '/fundraiser',
      name: 'FundRaiser',
      component: FundRaiser
    }*/
  ]
})



