<template>
  <div class="control-box">
    <h4>{{title}}</h4>
    <div class="slot-wrap">
      <slot></slot>
    </div>
  </div>
</template>

<script>

export default {
  name: 'control-box',
  props:{
    title: String
  }
}
</script>

<style scoped>
.control-box{
  display: block;
  border-style:solid;
  border-width: 1px;
  float:left;
  /*width:300px;
  height:300px;*/
  box-sizing: content-box;
  background: white;
  overflow:hidden;
}
.contract-box{
  margin-top:5px;
  margin-bottom:5px;
}
.slot-wrap{
  padding:20px;
}
.control-box h4{
  font-size:110%;
  text-indent: 20px;
  margin:0;
  padding-top: 10px;
  padding-bottom: 5px;
  background: #ccc;
}

#exchange .control-box{
  background: #1a1a1a;
  color: #ffbd1b;
  border-color: #ffbd1b;
  font-family: Lucida Console;
}

#exchange .control-box h4{
  background: #1a1a1a;
  background-repeat: no-repeat;
  border-bottom: 1px solid gold;
  color:#ffbd1b;
}


</style>



