<template>
  <div class="contract-data contract-box">
    <img v-if="img" :src="img"/>
    <div class="right">
      <h6>{{label}}</h6>
      <span>{{value}}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'contract-data',
    props:{
      label: String,
      value: String,
      img: String
    }
  }
</script>

<style scoped>
.contract-data{
  display: block;
}
.contract-data > img{
  max-width:32px;
  display: inline-block;
}
.contract-data .right{
  display: inline-block;
}
.contract-data .right:after{
  clear:both;
}
h6{
  margin:0;
}

.contract-data.rounded{
  padding:5px;
  border-radius:6px 6px 6px 6px;
  color:black;
}
.contract-data.rounded.left{
  padding:5px;
  border-radius:6px 0px 0px 6px;
}
.contract-data.rounded.right{
  padding:5px;
  border-radius:0px 6px 6px 0px;
}

#exchange .contract-data > img{
  filter: invert(100%);-webkit-filter: invert(100%);
}
#exchange .contract-data.rounded > img{
  filter: invert(0%);-webkit-filter: invert(0%);
}

</style>



