function sendTx(api, addr, ETH){
	return web3.eth.sendTransaction({
		from: state.currentAddress,
		to: addr,
		value: ETH?ETH:0,
		data: api.encodeABI()
	},(e,hash)=>{
		err(e)
	});
}

function err(e){
	console.log(e)
}


function rpcToBN(k){
	let BN = web3.utils.toBN( k.toString() )
	return BN
}

function weiForm(x){
	return web3.utils.toHex( web3.utils.toWei(x+'', "ether") )
}
function netAddress(addressPack){
	switch(CHAIN){
		case 1: return addressPack.eth
		case 2: return addressPack.etc
		case 0: return addressPack.test
	}
	
}
function showNumber(x,dec,toFix){
	if(!dec){ dec = 1; toFix=0; }
	let bigR = Big(x.toString())
	bigR = bigR.div(dec)
	let XZ = bigR.toFixed(toFix)
	return XZ;
}

function getPersists(ID,attr,_default){
	let responded = localStorage.getItem( "ticket"+ID+"_responded" );

	if(responded){//IS SET
		return localStorage.getItem("ticket"+ID+"_"+attr);
	}else{// IS NOT SET
		return _default
	}
}

function persist(ID, attr, val){
	let item = "ticket"+ID+"_"+attr
	localStorage.setItem(item, val)
}

function formatTime(hodl){
	//
	//console.log("hodl",hodl)
	if(hodl.hours>24*3){
		let hourLeft = hodl.hours % 24;
		let days = Math.floor(hodl.hours / 24);

		return days + ' Day'+(days>1?'s':'')+' ' +hourLeft + ' H'
	}
	else
		return hodl.hours + ' H ' +hodl.minutes + ' M'
}

function shuffle(array) {
  var currentIndex = array.length, temporaryValue, randomIndex;

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {

    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex -= 1;

    // And swap it with the current element.
    temporaryValue = array[currentIndex];
    array[currentIndex] = array[randomIndex];
    array[randomIndex] = temporaryValue;
  }

  return array;
}


